"use client";

import { useEffect, useMemo, useState } from "react";
import { supabase } from "../../../../lib/supabaseClient";
import { slugify } from "../../../../lib/slugify";
import { uploadProductImages } from "../../../../lib/uploadProductImages";
import styles from "./AdminProducts.module.css";

type Role = "admin" | "user" | "guest";

type FormState = {
  sku: string;
  name: string;
  slug: string;
  description: string;
  type: string;
  style: string;
  material: string;
  plasticType: string;
  industrialUse: string;
  placeOfOrigin: string;
  modelNumber: string;
  brandName: string;
  productName: string;
  colors: string;
  size: string;
  moq: string;
  logo: string;
  feature: string;
  sampleAvailable: boolean;
  packagingDetails: string;
};

const initialState: FormState = {
  sku: "",
  name: "",
  slug: "",
  description: "",
  type: "",
  style: "",
  material: "",
  plasticType: "",
  industrialUse: "",
  placeOfOrigin: "",
  modelNumber: "",
  brandName: "",
  productName: "",
  colors: "",
  size: "",
  moq: "",
  logo: "",
  feature: "",
  sampleAvailable: false,
  packagingDetails: ""
};

export default function NewProductPage() {
  const [role, setRole] = useState<Role>("guest");
  const [loading, setLoading] = useState(true);
  const [form, setForm] = useState<FormState>(initialState);
  const [slugEdited, setSlugEdited] = useState(false);
  const [files, setFiles] = useState<File[]>([]);
  const [status, setStatus] = useState<{ type: "error" | "success"; text: string } | null>(null);
  const [submitting, setSubmitting] = useState(false);

  const isAdmin = role === "admin";

  useEffect(() => {
    const loadRole = async () => {
      const { data: userData } = await supabase.auth.getUser();
      const user = userData.user;

      if (!user) {
        setRole("guest");
        setLoading(false);
        return;
      }

      const { data } = await supabase
        .from("profiles")
        .select("role")
        .eq("id", user.id)
        .single();

      setRole((data?.role as Role) || "user");
      setLoading(false);
    };

    loadRole();
  }, []);

  useEffect(() => {
    if (!slugEdited) {
      setForm((prev) => ({ ...prev, slug: slugify(prev.name) }));
    }
  }, [form.name, slugEdited]);

  const parsedColors = useMemo(
    () =>
      form.colors
        .split(",")
        .map((color) => color.trim())
        .filter(Boolean),
    [form.colors]
  );

  const handleChange = (field: keyof FormState, value: string | boolean) => {
    setForm((prev) => ({ ...prev, [field]: value }));
  };

  const handleSubmit = async (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    setStatus(null);
    setSubmitting(true);

    try {
      const { data, error } = await supabase
        .from("products")
        .insert({
          sku: form.sku,
          slug: form.slug || slugify(form.name),
          name: form.name,
          description: form.description || null,
          type: form.type || null,
          style: form.style || null,
          material: form.material || null,
          plastic_type: form.plasticType || null,
          industrial_use: form.industrialUse || null,
          place_of_origin: form.placeOfOrigin || null,
          model_number: form.modelNumber || null,
          brand_name: form.brandName || null,
          product_name: form.productName || null,
          colors: parsedColors.length ? parsedColors : null,
          size: form.size || null,
          moq: form.moq ? Number(form.moq) : null,
          logo: form.logo || null,
          feature: form.feature || null,
          sample_available: form.sampleAvailable,
          packaging_details: form.packagingDetails || null
        })
        .select("id")
        .single();

      if (error || !data) {
        throw error || new Error("Failed to insert product");
      }

      if (files.length) {
        const uploads = await uploadProductImages({
          supabase,
          productId: data.id,
          files
        });

        const imageRows = uploads.map((upload, index) => ({
          product_id: data.id,
          url: upload.url,
          alt: `${form.name} image ${index + 1}`,
          sort_order: index
        }));

        const { error: imageError } = await supabase
          .from("product_images")
          .insert(imageRows);

        if (imageError) {
          throw imageError;
        }
      }

      setStatus({ type: "success", text: "Product created successfully." });
      setForm(initialState);
      setFiles([]);
      setSlugEdited(false);
    } catch (err: unknown) {
      const message = err instanceof Error ? err.message : "Something went wrong.";
      setStatus({ type: "error", text: message });
    } finally {
      setSubmitting(false);
    }
  };

  if (loading) {
    return (
      <div className={styles.page}>
        <div className="container">Loading...</div>
      </div>
    );
  }

  if (!isAdmin) {
    return (
      <div className={styles.page}>
        <div className="container">
          <h1>Access denied</h1>
          <p>You do not have permission to view this page.</p>
        </div>
      </div>
    );
  }

  return (
    <div className={styles.page}>
      <div className="container">
        <div className={styles.header}>
          <h1>Add Product</h1>
          <p>Create a new product with supplier-ready specifications.</p>
        </div>

        <form className={styles.form} onSubmit={handleSubmit}>
          <div className={styles.grid}>
            <label className={styles.fieldLabel}>
              SKU *
              <input
                className={styles.fieldInput}
                required
                value={form.sku}
                onChange={(event) => handleChange("sku", event.target.value)}
              />
            </label>
            <label className={styles.fieldLabel}>
              Name *
              <input
                className={styles.fieldInput}
                required
                value={form.name}
                onChange={(event) => handleChange("name", event.target.value)}
              />
            </label>
            <label className={styles.fieldLabel}>
              Slug
              <input
                className={styles.fieldInput}
                value={form.slug}
                onChange={(event) => {
                  setSlugEdited(true);
                  handleChange("slug", event.target.value);
                }}
              />
            </label>
            <label className={`${styles.fieldLabel} ${styles.fullWidth}`}>
              Description
              <textarea
                className={styles.fieldTextarea}
                value={form.description}
                onChange={(event) => handleChange("description", event.target.value)}
              />
            </label>

            <label className={styles.fieldLabel}>
              Type
              <input
                className={styles.fieldInput}
                value={form.type}
                onChange={(event) => handleChange("type", event.target.value)}
              />
            </label>
            <label className={styles.fieldLabel}>
              Style
              <input
                className={styles.fieldInput}
                value={form.style}
                onChange={(event) => handleChange("style", event.target.value)}
              />
            </label>
            <label className={styles.fieldLabel}>
              Material
              <input
                className={styles.fieldInput}
                value={form.material}
                onChange={(event) => handleChange("material", event.target.value)}
              />
            </label>
            <label className={styles.fieldLabel}>
              Plastic Type
              <input
                className={styles.fieldInput}
                value={form.plasticType}
                onChange={(event) => handleChange("plasticType", event.target.value)}
                placeholder="PP/PET"
              />
            </label>
            <label className={styles.fieldLabel}>
              Industrial Use
              <input
                className={styles.fieldInput}
                value={form.industrialUse}
                onChange={(event) => handleChange("industrialUse", event.target.value)}
                placeholder="Beverage"
              />
            </label>
            <label className={styles.fieldLabel}>
              Place of Origin
              <input
                className={styles.fieldInput}
                value={form.placeOfOrigin}
                onChange={(event) => handleChange("placeOfOrigin", event.target.value)}
                placeholder="Zhejiang, China"
              />
            </label>
            <label className={styles.fieldLabel}>
              Model Number
              <input
                className={styles.fieldInput}
                value={form.modelNumber}
                onChange={(event) => handleChange("modelNumber", event.target.value)}
                placeholder="PP-1214/PET-1214"
              />
            </label>
            <label className={styles.fieldLabel}>
              Brand Name
              <input
                className={styles.fieldInput}
                value={form.brandName}
                onChange={(event) => handleChange("brandName", event.target.value)}
                placeholder="Jimi"
              />
            </label>
            <label className={styles.fieldLabel}>
              Product Name
              <input
                className={styles.fieldInput}
                value={form.productName}
                onChange={(event) => handleChange("productName", event.target.value)}
                placeholder="Custom Milk Tea Plastic PP PET Cup With Lids"
              />
            </label>
            <label className={styles.fieldLabel}>
              Colors (comma separated)
              <input
                className={styles.fieldInput}
                value={form.colors}
                onChange={(event) => handleChange("colors", event.target.value)}
                placeholder="Black, White"
              />
            </label>
            <label className={styles.fieldLabel}>
              Size
              <input
                className={styles.fieldInput}
                value={form.size}
                onChange={(event) => handleChange("size", event.target.value)}
              />
            </label>
            <label className={styles.fieldLabel}>
              MOQ
              <input
                className={styles.fieldInput}
                type="number"
                min="0"
                value={form.moq}
                onChange={(event) => handleChange("moq", event.target.value)}
                placeholder="5000"
              />
            </label>
            <label className={styles.fieldLabel}>
              Logo
              <input
                className={styles.fieldInput}
                value={form.logo}
                onChange={(event) => handleChange("logo", event.target.value)}
                placeholder="Customer's logo printing"
              />
            </label>
            <label className={styles.fieldLabel}>
              Feature
              <input
                className={styles.fieldInput}
                value={form.feature}
                onChange={(event) => handleChange("feature", event.target.value)}
                placeholder="Recyclable"
              />
            </label>
            <label className={styles.fieldLabel}>
              Packaging Details
              <input
                className={styles.fieldInput}
                value={form.packagingDetails}
                onChange={(event) => handleChange("packagingDetails", event.target.value)}
                placeholder="1000pcs/carton"
              />
            </label>
            <label className={styles.checkboxRow}>
              <input
                className={styles.checkboxInput}
                type="checkbox"
                checked={form.sampleAvailable}
                onChange={(event) => handleChange("sampleAvailable", event.target.checked)}
              />
              Sample Available
            </label>
            <label className={`${styles.fieldLabel} ${styles.fullWidth}`}>
              Images
              <input
                className={styles.fieldInput}
                type="file"
                accept="image/*"
                multiple
                onChange={(event) => setFiles(Array.from(event.target.files || []))}
              />
            </label>
          </div>

          {status && (
            <div
              className={status.type === "success" ? styles.success : styles.error}
              role="status"
            >
              {status.text}
            </div>
          )}

          <button className="btn btn-primary" type="submit" disabled={submitting}>
            {submitting ? "Saving..." : "Create product"}
          </button>
        </form>
      </div>
    </div>
  );
}
